import React from 'react';

class SearchBar extends React.Component {
  state = { 
    term: '',
    data: [
      {
        id: 1,
        text: "test 1"
      },
      {
        id: 2,
        text: "test 2"
      },
      {
        id: 3,
        text: "test 3"
      },
      {
        id: 4,
        text: "test 4"
      },
      {
        id: 5,
        text: "test 5"
      },
    ] 
  };

  onTermSubmit = event => {
    event.preventDefault();

    this.props.onSubmit(this.state.term);
  };

  render() {
    return (
      <div className="ui action input">
        <form className="ui form">
          <div className="field">
            <br></br><label>Photo keyword</label>
            <input
              type="text"
              placeholder="Search..."
              value={this.state.term}
              onChange={e => this.setState({ term: e.target.value })}
            />
            <button className="ui icon button" onClick = {this.onTermSubmit}>
            <i className="search icon"></i>
            </button>
            {this.state.term}
          </div>
          { this.state.data.map}
        </form>
      </div>
    );
  }
}

export default SearchBar;
