import React from 'react';
import Picture from '../Picture 1.png';
import '../Style.css';


class Profile extends React.Component {
    state ={clicks : 0};
    
    IncrementNum =() =>{
    this.setState({clicks : this.state.clicks + 1});
    }
  
   render () {
     return (
       <div>
         <h2 className ="pageheader">LimMinAh.com </h2>
      
         <div class="ui card">
            <div class="image">
             <img src={Picture} alt="profile pic"></img>
            </div>
            <div class="content">
              <span class="header"> Minah Lim </span>
              <div class="meta">
             <span class="date">07 Sep 1985</span>
            </div>
            <div class="description">Welcome to my world !</div>
           </div>
           <div class="extra content">
             <span>
               <i class="user icon" onClick={this.IncrementNum}></i>{this.state.clicks} followers</span>
          </div>
         </div>
       </div>
      );
     }
  }
  
  export default Profile;